const handleChannel = (data: any) => {
  const result = [];
  for (let i = 0; i < data.length; i++) {
    const item = data[i];
    switch (item.name) {
      case "":
        result[0] = item;
        break;
      case "":
        result[1] = item;
        break;
      case "":
        result[2] = item;
        break;
      case "":
        result[5] = item;
        break;
      case "":
        result[4] = item;
        break;
      case "":
        result[3] = item;
        break;

      default:
        result.push(item);
        break;
    }
  }
  return result;
};

